<template>
	<view class="tabs_box">
		<view style="white-space: nowrap;" v-if='type == 1' class="uni-tab-row">
		  <view  class="uni-tab-item"  v-for="(tab,index) in tabList" :key="index" :id="tab.id"  
		    :data-id="tab.id" @click="ontabtap">
		    <text class="uni-tab-item-title"  :class="{'uni-tab-item-title-active': styleType == 1 && active== tab.id,'uni-tab-item-title-active2': styleType == 2 && active== tab.id}" >{{tab.name}}</text>
		  </view>
		</view>
		<view style="white-space: nowrap;" class="uni-tab-row3"  v-if='type == 2'>
		  <view  class="uni-tab-item"  v-for="(tab,index) in tabList" :key="tab.id" :id="tab.id"   :ref="'tabitem'+ tab.id"
			:data-id="tab.id"  @click="ontabtap">
		   <text class="uni-tab-item-title"   :class="active== tab.id ? 'uni-tab-item-title-active' : ''">{{tab.name}}</text>
		  </view>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			tabList:{
				type: Array,
				default () {
					return [];
				}	
			},
			type:{
				type: [Number,String],
				default: 1
			},
			styleType:{
				type: [Number,String],
				default: 1
			}
		},
		name:"tabs",
		data() {
			return {
				active:0,
			};
		},
		methods:{
			ontabtap(e) {
				let x = e.target.dataset.id || e.currentTarget.dataset.id;
				if(x == this.active) return; 
				this.active = x
				this.$emit('change',x)
			},
		},
		onReady(){
			// this.$refs.tabs.active = 1
		},
	}
</script>

